
  
  .title {
    font-family: "monospace";
    font-size: 24px;
    font-weight: 700;
    color: #5D4037;
    text-align: center;
  }
  
  #content {
    line-height: 1.5em;
  }
  

  
  .project-container {
    padding: 40px 80px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    position: absolute;
    top: 20%;
  }
  
  .project-wrap {
    margin: 10px;
    transform: perspective(800px);
    transform-style: preserve-3d;
    cursor: pointer;
  }
  
  .project-wrap:hover .project-info {
    transform: translateY(0);
    transition: 0.6s ,cubic-bezier(0.23, 1, 0.32, 1);
  }
  
  .project-wrap:hover .project-info p {
    opacity: 1;
    transition: 0.6s ,cubic-bezier(0.23, 1, 0.32, 1);
  }
  
  .project-wrap:hover .project-info:after {
    transition: 5s ,cubic-bezier(0.23, 1, 0.32, 1);
    opacity: 1;
    transform: translateY(0);
  }
  
  .project-wrap:hover .project-bg {
    transition: 
      0.6s ,cubic-bezier(0.23, 1, 0.32, 1),
      opacity 5s ,cubic-bezier(0.23, 1, 0.32, 1);
    opacity: 0.8;
  }
  
  .project-wrap:hover .project {
    transition:
      0.6s ,cubic-bezier(0.23, 1, 0.32, 1),
      box-shadow 2s ,cubic-bezier(0.23, 1, 0.32, 1);
    box-shadow:
      rgba(white, 0.2) 0 0 40px 5px,
      rgba(white, 1) 0 0 0 1px,
      rgba(black, 0.66) 0 30px 60px 0,
      inset #333 0 0 0 5px,
      inset white 0 0 0 6px;
  }
  
  .project {
    position: relative;
    flex: 0 0 240px;
    width: 240px;
    height: 320px;
    background-color: #333;
    overflow: hidden;
    border-radius: 10px;
    box-shadow:
      rgba(black, 0.66) 0 30px 60px 0,
      inset #333 0 0 0 5px,
      inset rgba(white, 0.5) 0 0 0 6px;
    transition: 1s ,cubic-bezier(0.445, 0.05, 0.55, 0.95);
  }
  
  .project-bg {
    opacity: 0.5;
    position: absolute;
    top: -20px; left: -20px;
    width: 100%;
    height: 100%;
    padding: 20px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    transition:
      1s ,cubic-bezier(0.445, 0.05, 0.55, 0.95),
      opacity 5s 1s ,cubic-bezier(0.445, 0.05, 0.55, 0.95);
    pointer-events: none;
  }
  
  .project-info {
  
    position: absolute;
    bottom: 0;
    color: #fff;
    transform: translateY(40%);
    transition: 0.6s 1.6s cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  
  .project-info p {
    opacity: 0;
    text-shadow: rgba(black, 1) 0 2px 3px;
    transition: 0.6s 1.6s cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  
  .project-info * {
    position: relative;
    z-index: 1;
  }
  
  .project-info:after {
    content: '';
    position: absolute;
    top: 0; left: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to bottom, transparent 0%, rgba(#000, 0.6) 100%);
    background-blend-mode: overlay;
    opacity: 0;
    transform: translateY(100%);
    transition: 5s 1s ,cubic-bezier(0.445, 0.05, 0.55, 0.95);
  }
  
  .project-info h1 {
    font-family: "monospace";
    font-size: 36px;
    font-weight: 700;
    text-shadow: rgba(black, 0.5) 0 10px 10px;
    letter-spacing: -1px;
    color: #fff;
  }